<template>
  <div id="app" :style='getAppStyle' >
    <Results msg="Caesar salad"/>
  </div>
</template>

<script>
import Results from './components/Results.vue'

export default {
  name: 'app',
  data()  {
    return {
      width:0,
      styleObject:{
        overflow:'hidden',
         width:'700px',
      }
    }
},
  components: {
    Results
  },  
  computed:{
    getAppStyle() {
      return 'overflow:hidden;' + 'width:' + this.width + 'px;';
    }
  },
  created() {
    window.addEventListener('resize', this.handleResize);
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    handleResize() {
    
      this.width = window.innerWidth;

    }
  }

}
</script>

