<template >
    <div :style='resultsStyle'>
        <div id="chart-container" >
            <h2>Carbon check:</h2>
            <h1>{{msg}}</h1>
            <div :style="container2Style">
                <div :style="valueContainerStyle">
                    <div id="" :style="valueStyle"> 21.5</div>
                    <div :style="unitStyle">kG of GHGE</div>
                </div>
                <div :style="valueContainerStyle">
                    <div :style="valueStyle">12.3</div>
                    <div :style="unitStyle">average kG of GHGE<br/>per kG food</div>
                </div>
            </div>
            <canvas :style="chart" id="myChart"></canvas>
            <div :style="accuracyStyle">Click for more info</div>
        </div>
    </div>
</template>

<script>

import Chart from 'chart.js';


export default {
    name: 'Results',
    props: {
        msg: String
    },
    data()  {
        return {
            chartSize:0, 
            maxChartSize:600,
            windowWidth: 0,
            container2Style: "margin-top:30px;",
            resultsStyle:   "backgroundColor: #fdf0c8;" +
                            "height:calc(100vh - 28px);" +
                            "padding-top:28px;",
            valueContainerStyle: "width:50%;"+
                                    "float:left;",
            valueStyle: "fontSize:40px" ,
            unitStyle: "fontSize:15px" ,
            chart:"padding-top:54px;" +
            "padding-bottom:30px;",
            accuracyStyle:  "fontSize:14px;" +
                            "margin-right:12px;" ,
        }
    },
    created(){
        window.addEventListener('resize', this.handleResize);
    },
    mounted() {
        this.createChart();
        this.handleResize();
    },
    methods: {
        createChart(){ 
            var ctx = document.getElementById('myChart');
            var data = {
                datasets: [{
                    data: [10, 20, 30, 30],
                    backgroundColor: ['#fdd378', '#b9a2ff', '#ff93f9', "#81ffbc"],
                }],

                // These labels appear in the legend and in the tooltips when hovering different arcs
                labels: [
                    'Lettuce',
                    'Croutons',
                    'Chicken', 
                    'Anchovies'
                ]
            };
            // And for a doughnut chart
            var myDoughnutChart = new Chart(ctx, {
                type: 'doughnut',
                data: data,
                options: {
                    legend:{
                        labels:{
                            padding:10,
                            fontSize:14,
                            fontColor:"#7000bb"
                        },
                        position:'left',
                        usePointStyle: true,
                        
                    },
                    layout: {
                        padding: {
                            left: 0,
                            right: 0,
                            top: 0,
                            bottom: 0
                        }
                    },
                    animation:{
                        animateScale: true 
                    },
                    cutoutPercentage: 50,
                }
            });
            myDoughnutChart.canvas.parentNode.style.width = this.chartSize+'px';
            myDoughnutChart.canvas.parentNode.style.marginLeft= ( this.windowWidth/2 - this.chartSize/2)+'px';
        },

        /**
        * handle resize
        */
        handleResize() {
            this.windowWidth = window.innerWidth;
            if(this.windowWidth > this.maxChartSize){
                this.chartSize = this.maxChartSize ;
                document.getElementById('chart-container').style.width = this.chartSize + 'px';
            }
            else{
                this.chartSize = window.innerWidth;
                document.getElementById('chart-container').style.width = this.chartSize +'px';
            }
            document.getElementById('chart-container').style.marginLeft = ( this.windowWidth/2 - this.chartSize/2)+'px';
        },

        /**
        * handle resize
        */
        destroyed() {
            window.removeEventListener('resize', this.handleResize);
        },
        
     }

  };
</script>

